.dropdown-menu {
    position: absolute;
    width: 200px;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    border-radius: 8px;
    padding: 10px;
    cursor: move;
    z-index: 20; /* Asegúrate de que esté encima del canvas */
    user-select: none;
  }
  
  .dropdown-header {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .dropdown-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .dropdown-menu li {
    padding: 5px 0;
    cursor: pointer;
  }
  
  .dropdown-menu li:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  