.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px 20px;
    display: flex;
    justify-content: center; /* Centra los elementos en el navbar */
    align-items: center;
    z-index: 10;
    color: white;
  }
  
  .navbar-logo {
    position: absolute;
    left: 20px; /* Mantén el logo a la izquierda */
    font-size: 24px;
    font-weight: bold;
  }
  
  .navbar-menu {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navbar-item {
    position: relative;
    margin-left: 20px;
  }
  
  .navbar-item a {
    color: white;
    text-decoration: none;
    padding: 5px 10px;
  }
  
  .navbar-item a:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .dropdown {
    display: none;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.9);
    top: 100%;
    left: 0;
    min-width: 150px;
    list-style: none;
    padding: 10px 0;
    margin: 0;
  }
  
  .dropdown li a {
    padding: 10px;
    display: block;
  }
  
  .dropdown li a:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .navbar-item:hover .dropdown {
    display: block;
  }
  